.setup {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center; }

.setup-form {
  margin-top: 10%;
  width: 90%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  border-radius: 10px;
  padding: 30px;
  box-shadow: -5px 7px 20px 0px rgba(0, 0, 0, 0.75); }
  .setup-form label {
    margin: 20px 0;
    font-size: 20px; }
  .setup-form input {
    padding: .6em 1.4em .5em .8em;
    font-size: 20px;
    border-radius: 10px;
    border: 2px solid #494949; }
  .setup-form select {
    font-size: 20px;
    font-family: sans-serif;
    font-weight: 700;
    color: #444;
    line-height: 1.3;
    padding: .6em 1.4em .5em .8em;
    width: 100%;
    max-width: 100%;
    box-sizing: border-box;
    margin: 0;
    border: 1px solid #aaa;
    box-shadow: 0 1px 0 1px rgba(0, 0, 0, 0.04);
    border-radius: .5em;
    border: 2px solid #494949;
    background-color: #fff; }

.submit-button {
  margin-top: 20px;
  color: #494949 !important;
  text-transform: uppercase;
  background: #ffffff;
  padding: 20px;
  border: 2px solid #494949;
  border-radius: 6px;
  display: inline-block; }
  .submit-button:hover {
    color: #ffffff !important;
    background: #738598;
    border-color: white !important;
    -webkit-transition: all 0.4s ease 0s;
    transition: all 0.4s ease 0s; }

.quiz {
  margin-top: 10%;
  border-radius: 10px;
  padding: 30px;
  font-size: 20px;
  font-weight: bold; }

.question, .score {
  padding: 30px;
  text-align: center;
  list-style: none;
  box-shadow: -5px 7px 20px 0px rgba(0, 0, 0, 0.75); }
  .question li, .score li {
    margin-top: 20px;
    color: #494949 !important;
    text-transform: uppercase;
    background: #ffffff;
    padding: 5px;
    border: 2px solid #494949;
    border-radius: 6px;
    font-size: 16px; }
    .question li:hover, .score li:hover {
      color: #ffffff !important;
      background: #738598;
      border-color: white !important;
      -webkit-transition: all 0.4s ease 0s;
      transition: all 0.4s ease 0s; }

.score {
  background-color: lightslategray; }

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #dfe2e2; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

